
.blog-post {
  background-color: #f3f6ff;
  max-width: 700px;
  border-radius: 15px;
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  margin: 0 auto 24px auto;
  padding: 36px 48px;
  text-align: left;
}
.blog-post .icon {
    fill: #585a61;
    vertical-align: bottom;
    margin-right: 6px;
}
.blog-post .blog-title {
    color: #2f3243;
    font-size: 2.25em;
    margin: 0 0 6px 0;
}
.blog-post .blog-clear {
    text-align: initial;
    margin-top: 12px;
    position: relative;
    word-wrap: break-word;
}
.blog-post .blog-clear:after {
      content: '';
      clear: both;
      display: block;
}
.blog-post .blog-clear a {
      text-decoration: underline !important;
}
.blog-post .blog-clear iframe, .blog-post .blog-clear object embed {
      width: 67vw;
      max-width: 560px;
      height: 38vw;
      max-height: 316px;
}
.blog-post .blog-clear img {
      height: auto;
      max-width: 89%;
}
.blog-post .tags {
    margin-top: 6px;
}
.blog-post .labels {
    border: 2px solid #8c21ff;
    background: none;
    border-radius: 25px;
    margin: -3px 0 6px 3px;
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
    font-family: inherit;
    letter-spacing: inherit;
    color: #585a61;
}
.blog-post .labels:hover, .blog-post .labels:focus, .blog-post .labels.selected {
      cursor: pointer;
      background-color: #8c21ff;
      color: #f3f6ff;
}
.blog-post .labels-wrap {
      display: inline-block;
      width: calc(100% - 36px);
      vertical-align: top;
}



