
@font-face {
  font-family: 'coolvetica';
  src: url("./assets/fonts/coolvetica.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
a {
  text-decoration: none;
}
h1, h2 {
  margin-top: 0;
  text-transform: uppercase;
  font-weight: lighter;
}
p {
  font-size: 1.25em;
  margin: 24px 0;
}
body {
  min-width: 375px;
  background-color: #f3f6ff;
}
canvas {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 101;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.content {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.div-fade-enter-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.div-fade-leave-active {
  -webkit-transition: all 0.15s ease;
  transition: all 0.15s ease;
}
.div-fade-enter, .div-fade-leave-active {
  opacity: 0;
}
.section-header {
  display: inline-block;
  border-radius: 25px;
  padding: 6px 24px;
  color: #f3f6ff;
  background-color: #8c21ff;
}
#app {
  font-family: "coolvetica", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  letter-spacing: 1px;
  color: #585a61;
  font-size: .85em;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.link-btn {
  border: 2px solid #8c21ff;
  border-radius: 25px;
  padding: 6px 24px;
  color: #f3f6ff;
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  text-decoration: none;
}
.link-btn:hover {
    background-color: #8c21ff;
}
.link-btn:focus {
    background-color: #8c21ff;
}



